/* General */

* {
  padding: 0px;
  margin: 0px;
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
}

.centerIt {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  padding: 8px 10px;
  background-color:rgb(185, 200, 240);
  box-shadow: 1px 1px 5px rgb(105, 135, 216);
  border: none;
  font-size: 15px;
  font-weight: bold;
  border-radius: 8px;
  color: rgb(42, 82, 190);
  margin: 10px 0px;
}

.inpt {
  padding: 6px;
  margin: 15px 20px 15px 15px;
  font-size: 15px;
  width: 200px;
  border-radius: 5px;
  background-color:rgb(145, 170, 240);
}

/* AppPage*/
.App {
  background-color:rgb(185, 200, 240);
  border: 2px solid rgb(32, 65, 156);
  border-radius: 7px;
  height: 96.5vh;
  padding: 10px;
}

.title {
  color:rgb(171, 116, 197);
  height: 5%;
  font-family: 'Courier New', Courier, monospace, Times, serif;
}

.navTi {
  width: 150px;
  color: rgb(160, 32, 75);
  margin: 15px;
}


/** --> HomePage <-- **/
.homePage, .favorites {
  width: 95%;
  border: 2px solid rgb(32, 65, 156);
  border-radius: 7px;
  height: 80%;
  margin: auto;
  flex-direction: column;
}

.searchCityArea {
  height: 15%;
}

.weatherRsltsArea {
  border-top: 2px solid rgb(47, 76, 156);
  height: 85%;
  margin: 5px auto;
  padding: 15px 5px;
}

.crntRsltArea {
  justify-content: space-around;
  height: 15%;
}
 
.crntRslt h3 {
  color: rgb(160, 32, 75);
}

.crntRsltTxtArea {
  height: 15%;
  color: rgb(42, 82, 190);
  font-size: 25px;
}

.weekWeatherArea {
  height: 30%;
  justify-content: space-around;
  flex-direction: column;
}

.weatherTitle h3 {
  color: rgb(160, 32, 75);
}

.dailyWeatherArea {
  justify-content: space-around;
  width: 100%;
}

.dailyTemp {
  background-color:rgb(145, 170, 240);
  box-shadow: 1px 1px 3px rgb(169, 183, 223); 
  width: 55px;
  height: 60px;
  flex-direction: column;
  color: rgb(160, 32, 75);
}

@media (min-width: 500px) {
  .dailyWeatherArea{
    justify-content: space-around;
    width: 400px;
  }
}

@media (max-height: 433px) {
  .App {
    height: 100%;
  }
}

/** --> Favorites PAGE <-- **/
.favList{
  width: 90%;
  height: 90%;
  margin: auto;
  padding: 10px;
  display: flex;
  justify-content: flex-start;
  align-content: flex-start;
  flex-wrap: wrap;
  overflow: scroll;
}

/** --> Add to Favorites Page  <-- **/
.favListIt {
  border-radius: 5px;
  width: 90px;
  height: 100px;
  margin: 10px 8px;
  background-color: rgb(170, 189, 240);
  box-shadow: inset 1px 1px 5px 3px rgb(94, 123, 207),
              inset -1px -1px 5px 3px rgb(191, 200, 228);
} 

.favItemDel {
  margin-left: 55px;
}

.favDelBtn {
  margin-top: 6px;
  padding: 1.5px 1.5px -1px 1.5px;
  background-color: rgb(170, 189, 240);
  border: none;
  width: 20px;
  color: rgb(21, 21, 192);
}

.link {
  text-decoration-line: none;
}

.favListData  {
  height: 70%;
  flex-direction: column;
  justify-content: space-around;
  color: rgb(160, 32, 75);
  font-size: 20px;
  font-weight: 600; 
}

.cityKeyId {
  font-size: 15px;
}

